<template>
    <div id="board">
        <h2>Ingresa una Nueva Contrase単a</h2>
        <div id="msgPasswd">{{msg_passwd}}</div>
        <form id="form-passwd" @click="msgDisplay">
            <div class="form-floating">
                <input type="password" name="password" class="form-control" id="floatingPassword" placeholder="Password">
                <label for="floatingRePassword">Nueva Contrase単a</label>
            </div>

            <div class="form-floating">
                <input type="password" name="confirmation_password" class="form-control" id="floatingRePassword" placeholder="Password">
                <label for="floatingRePassword">Confirmar Contrase単a </label>
            </div>

            <button type="button" class="btn-send btn btn-primary mt-4" @click="sendPasswd">Enviar</button>
        </form>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'MyPasswdRecover',
    data(){
        return {
            msg_passwd: "",
        }
    },
    mounted(){
        this.msgDisplay();
    },
    methods: {
        sendPasswd(){
            const item_id= this.$route.params.id;
            const form = document.getElementById("form-passwd");
            const passwdData = new FormData(form);
            
            for (var pair of passwdData.entries()) {
                console.log(pair[0]+ ', ' + pair[1]); 
            }

            axios
                .put("/api/ForgotPassword/newPassword/"+item_id,passwdData,{
                    headers: {"Content-Type": "multipart/form-data",},
                })
                .then(response =>{
                    this.msg = response.data
                    if("ResetPassword" in this.msg){
                        this.msg_passwd = "Se cambio su contrase単a exitosamente"
                        console.log(this.msg.ResetPassword)
                        document.getElementById("msgPasswd").style.backgroundColor="#A3DDCB";
                        document.getElementById("msgPasswd").style.display="block";
                        location.href = "/login";
                    }else{
                        this.msg_passwd = "Error: " + this.msg.msj
                        document.getElementById("msgPasswd").style.backgroundColor="#DBE2EF";
                        document.getElementById("msgPasswd").style.display="block";
                    }
                    
                })
                .catch(error => {
                    console.log("Error: ", error);
                })
        },
        msgDisplay(){
            document.getElementById("msgPasswd").style.display="none";
        },
    }
}
</script>

<style scoped>

#board{
    height: 600px;
    text-align: center;
    padding-top: 7rem;
    padding-bottom: 9rem;
    background-color: #F2F5F9;
}

#msgPasswd{
    background-color: #DBE2EF;
    width: 300px;
    padding: 0.6rem 1.2rem 0.5rem 1.2rem;
    margin: 20px auto;
    display: none;
}


h2{
    padding-top: 5rem;
    margin-bottom: 2rem;
    color: #2c3e50;
}

form{
    width: 290px;
    margin: auto;
    margin-top: 30px;
    text-align: left;
}

input,button{
    width: 300px;
    padding: 0.5rem 1rem 0.5rem 1rem;
}

input{
    width: 320px;
    margin-top: 15px;
}

button{
    margin-top: 0;
    width: 320px;
    color: white;
    background-color: #111729;
    cursor: pointer;
}

.btn-send{
    background-color: #03506F !important;
    border-color: #03506F !important;
}

.btn-send:hover{
    background-color: #0f87b7 !important;
    border-color: #0f87b7 !important;
}


</style>
